import { NgModule } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http'
import { AppComponent } from './app.component';
import { ProductDetailsComponent } from './product-details/product-details.component';
import { ProductListComponent } from './product-list/product-list.component';
import { HeaderComponent } from './header/header.component';
import { ProductAlertsComponent } from './product-list/product-alerts/product-alerts.component';
import { RouterModule } from '@angular/router';
import { CarrinhoComponent } from './carrinho/carrinho.component';
import { EnviarProdutosComponent } from './enviar-produtos/enviar-produtos.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

@NgModule({
  declarations: [
    AppComponent,
      ProductListComponent,
      HeaderComponent,
      ProductAlertsComponent,
      ProductDetailsComponent,
      CarrinhoComponent,
      EnviarProdutosComponent
   ],
  imports: [
    BrowserModule,
    HttpClientModule,
    RouterModule.forRoot([
      {path: '', component: ProductListComponent},
      {path: 'products/:productId', component: ProductDetailsComponent},
      {path: 'carrinho', component: CarrinhoComponent},
      {path: 'precosDeEnvios', component: EnviarProdutosComponent}
    ]),
    ReactiveFormsModule,
    FormsModule
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
